<template>
<div>
  <el-dialog :title="'ITSM CI'" :visible.sync="showDialogFlag" width=1200px  v-on:close="close">
    <b-row>
      <b-col sm="3">
        <Tree
          v-bind:tree="tree"
          ref="tree"
          v-on:clickNode="clickNode"
        />
      </b-col>
      <b-col sm="9">
        <b-row>
          <b-col sm="12">
            <div class="search-component">
              <input class="form-control mt-1" style="width: 100%;" v-model="searchParam" v-on:keyup.enter="enterSearch" v-bind:placeholder=labels.search />
            </div>
          </b-col>
        </b-row>
        <b-row>
          <b-col sm="12">
            <p class="grid-line"></p>
            <data-tables-server ref="listGrid" :table-props='listGrid.tableProps' :data="listGrid.data" :filters="listGrid.filters" :total="listGrid.total" v-on:query-change="getListData" :pagination-props="{pageSizes: [5, 10, 15] }" :current-page.sync='listGrid.currentPage'>
              <el-table-column :width='listGrid.titles[0].width'>
                <template slot-scope="props">
                  <el-checkbox class='single' v-model="props.row.chk" v-on:change="selectGridRow(props.row)"/>
                </template>
              </el-table-column>
              <el-table-column :prop='listGrid.titles[1].prop' :label='labels.classstructureId' sortable='custom' :width='listGrid.titles[1].width'></el-table-column>
              <el-table-column :prop='listGrid.titles[2].prop' :label='"ITSM CIID"'             sortable='custom' :width='listGrid.titles[2].width'></el-table-column>
              <el-table-column :prop='listGrid.titles[3].prop' :label='labels.name'             sortable='custom' ></el-table-column>
              <el-table-column :prop='listGrid.titles[4].prop' :label='labels.status'           sortable='custom' :width='listGrid.titles[3].width'></el-table-column>
              <el-table-column align="right" width="80">
                <template  slot-scope="props">
                  <el-button v-on:click="openChildDialog(props.row)" :title="labels.move" style="padding: 0; width:30px; height: 30px;">
                    <img src="@/assets/img/icons/icon_search_login.png" width="60%" />
                  </el-button>
                </template>
              </el-table-column>
            </data-tables-server>
          </b-col>
        </b-row>
      </b-col>
    </b-row>
    <b-row>
      <div class="col mt-2">
        <div class='d-flex justify-content-end'>
          <b-button variant="primary" class="px-3 sm" v-on:click="add" >{{labels.add}}</b-button>
          <b-button variant="primary" class="px-3 sm" v-on:click="close">{{labels.close}}</b-button>
        </div>
      </div>
    </b-row>
  </el-dialog>
  <el-dialog :title="'ITSM CI SPEC'" :visible.sync="showChildDialogFlag" width=800px  v-on:close="closeChildDialog">
    <b-row>
      <b-col sm="12">
        <div class="search-component">
          <input class="form-control mt-1" style="width: 100%;" v-model="childGrid.searchParam" v-on:keyup.enter="enterSearchChild" v-bind:placeholder=labels.search />
        </div>
      </b-col>
    </b-row>
    <b-row>
      <b-col sm="12">
        <p class="grid-line"></p>
        <data-tables border layout='table, pagination' :table-props='childGrid.tableProps' :data='childGrid.data' :filters='childGrid.filters' :page-size='childGrid.pageProps.pageSize' :pagination-props='childGrid.pageProps'>
          <el-table-column :prop='"ciAttrName"'      :label='labels.item'   width='200' sortable='custom'></el-table-column>
          <el-table-column :prop='"ciAttrValueLast"' :label='labels.value'  sortable='custom'></el-table-column>
        </data-tables>
      </b-col>
    </b-row>
    <b-row>
      <div class="col mt-2">
        <div class='d-flex justify-content-end'>
          <b-button variant="primary" class="px-3 sm" v-on:click="closeChildDialog">{{labels.close}}</b-button>
        </div>
      </div>
    </b-row>
  </el-dialog>
</div>
</template>
<script>
import Tree from '@/views/common/Tree'
export default {
  name: 'CI',
  props: ['showDialogFlagProp', 'param'],
  components: {
    'Tree': Tree
  },
  data () {
    return {
      labels: {
        classstructureId: ''
      },
      tree: {
        dataType: 'RAW',
        data: [],
        rawData: [],
        selectData: {},
        isRootDataFlag: true,
        rootData: {text: 'CLASSSTRUCTURE', data: {depthFullid: 'CLASSSTRUCTURE', hierarchypath: '', classicon: 'sheets-48.png'}, children: []},
        rootVal: 'IT_ASSET',
        parentColNm: 'parentCiTypeId',
        childColNm: 'ciTypeId',
        textColNm: 'ciTypeName',
        iconImgType: 'svg',
        showTreeFlag: false,
        isUseCntFlag: false,
        expand: {data: {ciTypeId: 'HARDWARE'}},
        showNodeIconFn: function (node) {
          return '<svg id="Solid" viewBox="0 0 32 32" xmlns="http://www.w3.org/2000/svg"><path class="cls-1" d="M29.74,11.61a.58.58,0,0,0,0-.18.19.19,0,0,0,0-.07.83.83,0,0,0-.14-.22L24,5.47a.86.86,0,0,0-.22-.15l-.07,0a.65.65,0,0,0-.19,0H3A.76.76,0,0,0,2.25,6V26a.76.76,0,0,0,.75.75H29a.76.76,0,0,0,.75-.75V11.66A.43.43,0,0,1,29.74,11.61Zm-1.49.8h-4.8a.75.75,0,0,1-.75-.75V6.75h.44L24.2,7.84l3,3.07,1,1.06Z"/></svg>'
        },
        shwoAddIconFn: function (node) {
          return false
        }
      },
      listGrid: {
        tableProps: {
          border: false,
          stripe: true,
          headerCellStyle: function () {
            return 'font-family: sans-serif; font-size: 14px; color: #28313b; font-weight: bold; width: 70%; height:60px'
          }
        },
        currentPage: 1,
        rowIndex: -1,
        pageProps: {
          pageSize: 10,
          pageSizes: [10, 20, 30]
        },
        data: [],
        dataBak: [],
        filters: [{
          prop: [],
          value: ''
        }, {
          prop: 'tree',
          value: ''
        }],
        filteredData: [],
        selection: [],
        titles: [{
          prop: 'chk',
          width: '30px'
        }, {
          prop: 'ciTypeNm',
          width: '200px'
        }, {
          prop: 'kaiCiid',
          width: '110px'
        }, {
          prop: 'ciName',
          width: '130px'
        }, {
          prop: 'usestatus',
          width: '150px'
        }],
        total: 0
      },
      childGrid: {
        tableProps: {
          border: false,
          stripe: true,
          headerCellStyle: function () {
            return 'font-family: sans-serif; font-size: 14px; color: #28313b; font-weight: bold; width: 70%; height:60px'
          }
        },
        currentPage: 1,
        rowIndex: -1,
        pageProps: {
          pageSize: 5,
          pageSizes: [5, 10]
        },
        data: [],
        filters: [{
          prop: ['ciAttrName', 'ciAttrValueLast'],
          value: ''
        }],
        filteredData: [],
        currentData: {},
        total: 0,
        searchParam: ''
      },
      selectedItem: {},
      // Global Var
      orgId: '',
      searchParam: '',
      ciDialogParam: '',
      showDialogFlag: false,
      showChildDialogFlag: false
    }
  },
  watch: {
    'showDialogFlagProp': function (val) {
      this.showDialogFlag = val
      if (val) {
        this.selectedItem = {}
        this.tree.showTreeFlag = false
        this.initialize()
        setTimeout(() => {
          this.listGrid.currentPage = 1
          this.searchParam = ''
          this.listGrid.filters[0].value = '/'
          this.listGrid.filters[0].value = ''
          this.listGrid.filters[1].value = ''
        }, 100)
      }
    }
  },
  methods: {
    // INIT FN ////////////////////////////////////////////
    initialize: function () {
      this.$util.setLabels(this.$options.name, this.labels)
      this.orgId = this.$store.getters.getUser.orgId
      this.getInitData()
    },
    // USER FN ////////////////////////////////////////////
    // TRNS FN ////////////////////////////////////////////
    getInitData: function () {
      this.$http.get(
        '/episode/api/asset/resource/ci/itsmclasslist'
      ).then(res => {
        this.tree.rawData = res.data
      }).catch(error => {
        console.log(error)
      })
    },
    getListData: function (srchCnd) {
      let page = 1
      let pageSize = 5
      let orderBy = 'id'
      let sort = 'ascending'
      let searchParam = ''
      let treeSearchParam = ''
      if (srchCnd !== null && srchCnd !== undefined && srchCnd !== 'undefined') {
        page = srchCnd.page
        pageSize = srchCnd.pageSize
        if (srchCnd.sort.prop != null) {
          orderBy = srchCnd.sort.prop
          sort = srchCnd.sort.order
        }
        searchParam = srchCnd.filters[0].value
        treeSearchParam = srchCnd.filters[1].value
      }
      this.$http.get(
        '/episode/api/asset/resource/ci/itsmcilist?page=' + page + '&pageSize=' + pageSize + '&orderBy=' + orderBy + '&sort=' + sort +
        '&orgId=' + this.orgId + '&searchParam=' + encodeURI(searchParam) + '&treeSearchParam=' + treeSearchParam + '&ciDialogType=' + this.param.ciDialogType + '&ciDialogParam=' + this.param.ciDialogParam
      ).then(res => {
        this.listGrid.data = res.data.data
        this.listGrid.total = res.data.total
        let chkedList = this.listGrid.data.filter((data) => data.chk === true)
        chkedList.forEach(data => {
          data.chk = false
        })
      }).catch(error => {
        console.log(error)
      })
    },
    // EVENT FN ///////////////////////////////////////////
    clickNode: function (node) {
      this.searchParam = ''
      this.listGrid.filters[0].value = ''
      if (!this.$util.isNull(node)) {
        let filterVal = (node.data.depthFullid === 'CLASSSTRUCTURE' ? '' : node.data.depthFullid)
        this.listGrid.filters[1].value = filterVal
      }
    },
    filterFocus: function () {
      // this.listGrid.filters[0].value = ''
    },
    enterSearch: function () {
      this.listGrid.filters[0].value = this.searchParam
    },
    selectGridRow: function (row) {
      let chkedList = this.listGrid.data.filter((data) => data.chk === true)
      chkedList.forEach(data => {
        if (data.id !== row.id) {
          data.chk = false
        }
      })
      this.selectedItem = row
    },
    openChildDialog: function (row) {
      this.showChildDialogFlag = true
      this.$http.get(
        '/episode/api/asset/resource/ci/itsmcispec?ciId=' + row.ciId
      ).then(res => {
        this.childGrid.data = res.data
      }).catch(error => {
        console.log(error)
      })
    },
    closeChildDialog: function () {
      this.showChildDialogFlag = false
    },
    add: function () {
      this.$emit('add', this.selectedItem)
    },
    close: function () {
      this.$emit('close')
    },
    enterSearchChild: function () {
      this.childGrid.filters[0].value = this.childGrid.searchParam
    }
  },
  created: function () {}
}
</script>



